import React, {useState} from "react";

import Pills from "./Pills";
import Page from "./Page";
import {Button, Modal, ModalBody, ModalFooter, ModalHeader, TabContent, TabPane, Input, Table, Alert} from 'reactstrap'
import data from './data'

function randomInteger(min, max) {
    // получить случайное число от (min-0.5) до (max+0.5)
    let rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(rand);
}
export default () => {
    function selectText(event) {
        event.target.select()
    }

    const [activeTab, setActiveTab] = useState('1');
    const toggle = tab => {
        setActiveTab(tab);
    };

    const [infoModal, setInfoModal] = useState(false);
    const [checkoutModal, setCheckoutModal] = useState(false);
    const [postCheckoutModal, setPostCheckoutModal] = useState(false);

    const toggleInfoModal = () => {
        setInfoModal(!infoModal);
        setCheckoutModal(false)
    };
    const toggleCheckoutModal = () => {
        setCheckoutModal(!checkoutModal);
        setInfoModal(false)
    };
    const togglePostCheckoutModal = () => {
        setPostCheckoutModal(!postCheckoutModal);
        setCheckoutModal(false)
    };

    const [modalData, setModalData] = useState({
        id: 0,
        title: '',
        text: '',
        btnText: '',
        price: '',
        comment: randomInteger(1000000, 9999999)
    });

    const number = "+79521344070";

    const tabs = data.map((el, i) => {
        return (
            <TabPane key={i} tabId={`${i + 1}`}>
                <Page setModalData={setModalData}
                      modalData={modalData}
                      toggleInfoModal={toggleInfoModal}
                      toggleCheckoutModal={toggleCheckoutModal}
                      data={el}/>
            </TabPane>
        )
    });

    return (
        <section className="container pages" id="pages">
            <h3 className="additional-headline">
                Товары
            </h3>
            <Pills activeTab={activeTab} toggle={toggle}/>

            <TabContent activeTab={activeTab}>
                {tabs}
            </TabContent>

            <Modal isOpen={infoModal} toggle={toggleInfoModal}>
                <ModalHeader toggle={toggleInfoModal}>{modalData.title}</ModalHeader>
                <ModalBody>
                    {modalData.text}
                </ModalBody>
                <ModalFooter>
                    <Button color="first" onClick={toggleCheckoutModal}>{modalData.btnText}</Button>
                    <Button color="secondary" onClick={toggleInfoModal}>Закрыть</Button>
                </ModalFooter>
            </Modal>
            <Modal isOpen={postCheckoutModal} toggle={togglePostCheckoutModal}>
                <ModalHeader toggle={togglePostCheckoutModal}>{modalData.title}</ModalHeader>
                <ModalBody>
                    <Table>
                        <tbody>
                        <tr>
                            <th>Товар</th>
                            <td>{modalData.title}</td>
                        </tr>
                        <tr>
                            <th>Количество</th>
                            <td>{1}</td>
                        </tr>
                        <tr>
                            <th>Сумма</th>
                            <td>{`${modalData.price}.00 руб.`}</td>
                        </tr>
                        <tr>
                            <th>QIWI Кошелек</th>
                            <td><Input onClick={selectText} value={number}/></td>
                        </tr>
                        <tr>
                            <th>Комментарий к платежу</th>
                            <td><Input onClick={selectText} value={`${modalData.id}` + `${modalData.comment}`}/></td>
                        </tr>
                        </tbody>
                    </Table>
                    <Alert color="danger" className="w-100 p-3">Внимание! Очень важно чтобы вы переводили деньги с этим
                        примечанием, иначе средства не будут зачислены автоматически.</Alert>
                </ModalBody>
                <ModalFooter>
                    <Button color="first" onClick={togglePostCheckoutModal}>{modalData.btnText}</Button>
                    <Button color="secondary" onClick={togglePostCheckoutModal}>Закрыть</Button>
                </ModalFooter>
            </Modal>
            <Modal isOpen={checkoutModal} toggle={toggleCheckoutModal}>
                <ModalHeader toggle={toggleCheckoutModal}>{modalData.title}</ModalHeader>
                <ModalBody>
                    <Table>
                        <tbody>
                            <tr>
                                <th>Товар</th>
                                <td>{modalData.title}</td>
                            </tr>
                            <tr>
                                <th>Сумма за шт.</th>
                                <td>{`${modalData.price}.00 руб.`}</td>
                            </tr>
                            <tr>
                                <th>Количество</th>
                                <td><Input defaultValue={1} className="w-25"/></td>
                            </tr>
                            <tr>
                                <th>Ваш Email</th>
                                <td><Input placeholder="example@mail.com"/></td>
                            </tr>
                        </tbody>
                    </Table>
                </ModalBody>
                <ModalFooter>
                    <Button color="first" onClick={togglePostCheckoutModal}>Продолжить</Button>
                    <Button color="secondary" onClick={toggleCheckoutModal}>Закрыть</Button>
                </ModalFooter>
            </Modal>
        </section>
    )
}